@import '../../styles/breakpoints';

// SB Navbar Variables:
// --sb-navbar-bg-color  -   Background color of navbar
// --sb-navbar-elevation -   Elevation for navbar
// --sb-navbar-col-gap   -   Gap between columns in areas

.sb-navbar {
  align-items: center;
  background-color: var(--sb-navbar-bg-color, var(--sbt-color-header-dark));
  box-shadow: var(--sb-navbar-elevation, var(--sbt-elevation-40));

  display: grid;
  gap: var(--sb-navbar-col-gap, var(--sbt-spacing-50));
  grid-template-areas: 'brand nav utility';
  grid-template-columns: auto 1fr auto;
  min-height: var(--sbt-navbar-height);
  row-gap: 0;

  @include window-width-max-small {
    --sb-navbar-col-gap: var(--sbt-spacing-10);

    grid-template-areas: 'brand utility navbtn' 'nav nav nav';
    grid-template-columns: auto 1fr auto;
    height: auto;
    min-height: 0;
  }
}

.sb-navbar__sticky {
  position: sticky;
  top: 0;
  width: 100%;
  z-index: calc(var(--sbt-stack-modal) + 100);
}
