@import '../../../styles/breakpoints';

// --sb-brand__logo-height

.sb-brand {
  grid-area: brand;
  padding: var(--sbt-spacing-30);
}

.sb-brand__link {
  align-items: center;
  display: flex;
  text-decoration: none;
}

.sb-brand__logo {
  height: var(--sb-brand__logo-height, 2rem);
  max-height: var(--sbt-navbar-height);
}

.sb-brand__text {
  --sb-typography-color: var(--sb-brand__text-color, var(--sbt-white));
  line-height: var(--sbt-line-height-100);
  padding-left: var(--sbt-spacing-20);
}

.sb-brand__hide-on-mobile {
  @include window-width-max-small {
    display: none;
  }
}
