@import '../../../styles/breakpoints';

.sb-controls {
  align-items: center;
  display: flex;
  gap: var(--sbt-spacing-60);
  grid-area: nav;
  height: var(--sbt-navbar-height);
  justify-content: flex-start;
  margin-left: var(--sbt-spacing-60);
  max-height: var(--sbt-navbar-height);
  overflow: hidden;
  row-gap: var(--sbt-spacing-40);
  visibility: visible;

  @include window-width-max-small {
    align-items: flex-start;
    display: flex;
    flex-direction: column;
    height: auto;
    margin-left: 0;
    max-height: var(--sb-controls-max-height, 500px);
    padding-bottom: var(--sbt-spacing-40);
    transition: visibility 0s linear, max-height 0.3s ease-in-out;
  }
}

.sb-controls__collapsed {
  @include window-width-max-small {
    max-height: 0;
    padding: 0;
    transition: visibility 0.3s linear, max-height 0.3s cubic-bezier(0, 1, 0, 1);
    visibility: hidden;
  }
}

.sb-controls__button {
  --sb-button-icon-color: var(--sbt-white);
  grid-area: navbtn;

  @include window-width-min-small {
    display: none;
  }
}
