// Specificity increased to win over tippy
.sb-popover.sb-popover[data-theme~='light-border'] {
  // V13 change these tippy defaults to be closer to our stuff
  border: var(--sb-popover-border, 1px solid rgba(0, 8, 16, 0.15));
  border-radius: var(--sb-popover-border-radius, 4px);
  box-shadow: var(--sb-popover-box-shadow, 0 4px 14px -2px rgb(0 8 16 / 8%));

  .tippy-content {
    padding: var(--sb-popover-content-padding, 5px 9px);
  }
}
