// Tooltip Variables:
// --sb-tooltip-border-radius
// --sb-tooltip-font-size
// --sb-tooltip-font-weight
// --sb-tooltip-line-height
// --sb-tooltip-padding
// --sb-tooltip-content-align

.sb-tooltip {
  /* stylelint-disable-next-line selector-class-pattern -- Third Party Class */
  .tippy-content {
    border-radius: var(
      --sb-tooltip-border-radius,
      var(--sbt-border-radius-small)
    );
    font-size: var(--sb-tooltip-font-size, var(--sbt-font-size-25));
    font-weight: var(--sb-tooltip-font-weight, var(--sbt-font-weight-70));
    line-height: var(--sb-tooltip-line-height, var(--sbt-line-height-50));
    padding: var(
      --sb-tooltip-padding,
      var(--sbt-spacing-20) var(--sbt-spacing-40)
    );
    text-align: var(--sb-tooltip-content-align, center);
  }
}
