@import '../../styles/breakpoints';

// SB Typography Variables:
// --sb-typography-color
// --sb-typography-bottom-gutter

// --sb-typography-T100-font-size
// --sb-typography-T100-font-weight
// --sb-typography-T100-line-height
// ...
// --sb-typography-T05-font-size
// --sb-typography-T05-font-weight
// --sb-typography-T05-line-height
// --sb-typography-bold

// PRISM Responsive Screen sizes breakpoints
// Compact: 0 - 767px
// Regular (default):  768px - 1239px
// Expanded Standard:  1240+

.sb-typography {
  color: var(--sb-typography-color, inherit);
}

.sb-typography_with-bottom-gutter {
  margin-bottom: var(--sb-typography-bottom-gutter, 0.35em);
}

.sb-typography_clickable {
  color: var(--sbt-theme-color-70);
  cursor: pointer;

  &:hover {
    color: var(--sbt-theme-color-80);
  }
}

.sb-typography_italic {
  font-style: italic;
}

.sb-typography_T100 {
  font-size: var(--sb-typography-T100-font-size, var(--sbt-font-size-100));
  font-weight: var(--sb-typography-T100-font-weight, var(--sbt-font-weight-40));
  line-height: var(--sb-typography-T100-line-height, var(--sbt-line-height-20));

  &.sb-typography_responsive {
    @include window-width-max-small() {
      font-size: var(--sb-typography-T100-font-size, var(--sbt-font-size-80));
    }

    @include window-width-min-large() {
      font-size: var(--sb-typography-T100-font-size, 5rem);
    }
  }
}

.sb-typography_T90 {
  font-size: var(--sb-typography-T90-font-size, var(--sbt-font-size-90));
  font-weight: var(--sb-typography-T90-font-weight, var(--sbt-font-weight-40));
  line-height: var(--sb-typography-T90-line-height, var(--sbt-line-height-20));

  &.sb-typography_responsive {
    @include window-width-max-small() {
      font-size: var(--sb-typography-T90-font-size, 2.25rem);
      line-height: var(
        --sb-typography-T90-line-height,
        var(--sbt-line-height-30)
      );
    }

    @include window-width-min-large() {
      font-size: var(--sb-typography-T90-font-size, 4.625rem);
    }
  }
}

